@use 'base/variables' as v;
@use 'base/mixins' as m;

.blog {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    &__Box {}

    &__imagenes {
        width: 100%;
    }

    &__artInfoBlog {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
    }

    &__H5 {
        font-size: 1.4rem;
    }
}